.form {
	&__title {
		letter-spacing: .0001em; }
	&__wrap {
		margin-top: 36px;
		width: 382px;
		@include sm {
			margin-top: 24px;
			width: 100%; } }
	&__footer {
		margin-top: 46px;
		margin-left: -4px;
		@include sm {
			margin: 37px 0 0; } }
	&__btn {
		width: 100%; } }

.trigger {
	position: fixed;
	bottom: 0;
	margin-left: -56px;
	z-index: 100;
	padding: 27px 75px 27px 55px;
	width: 494px;
	color: $spray;
	font-family: $montserrat;
	font-size: 24px;
	line-height: 1;
	letter-spacing: -0.0003em;
	font-weight: 700;
	text-transform: uppercase;
	border-radius: 12px 12px 0 0;
	box-shadow: 0 17px 39px 0 rgba(0,0,0,0.41);
	transition: opacity .3s, visibility .3s;
	background: $outerSpace;
	display: block;
	@include lg {
		display: none; }
	&.is-hidden {
		opacity: 0;
		visibility: hidden; }
	&:before {
		position: absolute;
		content: "";
		top: 50%;
		margin-top: -9px;
		right: 54px;
		@include arr(22,18,#FFE886,t); } }

.field {
	position: relative;
	display: block;
	&:not(:first-child) {
		margin-top: 40px;
		@include sm {
			margin-top: 28px; } }
	&.is-active {
		.field__label {
			opacity: 0.5;
			transform: translate3d(0,-73%,0); } }
	&__label {
		position: absolute;
		top: 2px;
		left: 0;
		right: 0;
		color: $white;
		font-family: $lato;
		@include f(15, 1.3, -0.02, 700);
		transition: transform 0.3s, opacity 0.3s;
		pointer-events: none; } }


.input,
.textarea {
	padding: 12px 0;
	width: 100%;
	height: 46px;
	font-family: $lato;
	color: $white;
	font-family: $lato;
	@include f(15, 1.3, -0.02, 700);
	border: none;
	border-bottom: 1px solid rgba($white, 0.3);
	border-radius: 0 !important;
	background-color: transparent;
	display: block;
	@include sm {
		height: 42px; } }

.textarea {
	resize: none; }

.btn {
	padding: 23px 40px;
	font-family: $montserrat;
	font-size: 24px;
	line-height: 1;
	font-weight: 700;
	vertical-align: top;
	text-transform: uppercase;
	border-radius: 0;
	transition: background-color .3s, box-shadow .3s;
	box-shadow: 0 7px 15px 0 rgba(0,0,0,0.50);
	background-color: $spray;
	display: inline-block;
	@include sm {
		padding: 17px 40px; }
	@include hover {
		background-color: darken($spray, 20);
		box-shadow: 0 2px 10px 0 rgba(0,0,0,0.50); }
	.icon-arr-right {
		font-size: 20px;
		vertical-align: top;
		margin-top: 1px; } }
