.title-xl,
.title-lg {
	font-family: $montserrat;
	@include f(48, 0.96, 1.4px, 300);
	text-transform: uppercase;
	@include md {
		@include f(36); } }

.title-big {
	font-family: $montserrat;
	@include f(64, 0.93, 1.8px, 700);
	text-transform: uppercase;
	@include sm {
		font-size: 43px; } }

.title-xl {
	@include sm {
		@include f(30, 1.07, 0.9px); } }
.title-lg {
	@include sm {
		@include f(28, 1.14, 0.8px); } }

.title-md {
	font-family: $montserrat;
	@include f(20, 1.1, .6px, 300);
	strong {
		font-size: 24px;
		font-weight: 700; }
	&__in {
		&_mob {
			@include sm {
				font-size: 15px; } } } }

.title-sm {
	font-family: $montserrat;
	@include f(16, 1.25, 0.5px, 700); }

.description {
	@include f(15, 1.33);
	@include sm {
		@include f(14, 1.29); }
	p + p {
		margin-top: 19px; } }
