.footer {
	position: relative;
	color: $white;
	background-color: $limedSpruce;
	@include lg {
		background-color: $black; }
	&:before {
		position: absolute;
		content: "";
		left: 0;
		right: 0;
		bottom: 0;
		height: 10px;
		background-color: $black; }
	&__inner {
		padding-bottom: 37px;
		@include sm {
			padding-bottom: 20px; } }
	&__form {
		position: absolute;
		bottom: -24px;
		right: 135px;
		padding: 40px 28px 68px 56px;
		width: 494px;
		background-color: $outerSpace;
		box-shadow: 0 17px 39px 0 rgba(0,0,0,0.41);
		border-radius: 12px;
		@include xl {
			right: 70px;
			border-radius: 0; }
		@include lg {
			position: relative;
			padding: 80px 28px;
			bottom: auto;
			right: auto;
			margin: 0 auto;
			width: auto;
			max-width: 494px;
			box-shadow: none;
			background-color: $limedSpruce; }
		@include sm {
			padding: 30px 0 55px; }
		&:before,
		&:after {
			position: absolute;
			content: "";
			top: 0;
			bottom: 0;
			width: 50vw;
			background-color: $limedSpruce;
			display: none;
			@include lg {
				display: block; } }
		&:before, {
			left: 100%; }
		&:after {
			right: 100%; } }
	&__top {
		margin-top: 80px;
		@include sm {
			margin-top: 24px; } }
	&__bottom {
		margin-top: 70px;
		@include clr;
		@include sm {
			margin-top: 38px; } }
	&__logo {
		max-width: 57px;
		display: block;
		@include lg {
			float: right; }
		img {
			max-width: 100%;
			display: block; } }
	&__contacts {
		padding-right: 500px;
		@include lg {
			padding-right: 0; } } }

.contacts {
	font-family: $lato;
	font-size: 15px;
	line-height: 1.3;
	font-weight: 400;
	display: table;
	@include sm {
		display: block; }
	&__col {
		vertical-align: top;
		display: table-cell;
		@include sm {
			display: block; }
		&:not(:first-child) {
			padding-left: 55px;
			@include sm {
				padding-left: 0;
				margin-top: 25px; } } }
	&__title {
		margin-bottom: 12px;
		font-family: $montserrat;
		font-size: 16px;
		font-weight: 700; }
	a {
		color: inherit;
		border-bottom: 1px solid transparent;
		transition: border-color .3s;
		@include hover {
			border-color: currentColor; } } }

.ready {
	padding: 100px 0 103px;
	background-color: $brightSun;
	@include sm {
		padding: 30px 0 35px; }
	&__subtitle {
		margin-bottom: 3px;
		letter-spacing: 0.03em;
		color: $dark;
		font-family: $montserrat;
		font-size: 20px;
		font-weight: 300;
		text-transform: uppercase;
		@include sm {
			margin-bottom: 9px; } }
	&__title {
		max-width: 490px;
		@include sm {
			margin-right: -16px; } } }
